*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  color: inherit;
  font: inherit;
  line-height: inherit;
}

html {
  hanging-punctuation: first last;
}

body {
  line-height: 1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

div,
p,
li {
  text-wrap: pretty;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}

img, picture, video {
  display: block;
  width: 100%;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
    scroll-padding-top: 1rem;
  }
}
.content-grid {
  --breakout-size: calc((var(--template-breakout-max-width) - var(--template-max-width)) / 2);
  display: grid;
  grid-template-columns: [full-width-start] minmax(var(--template-padding), 1fr) [content-start] min(100% - var(--template-padding) * 2, var(--template-max-width) - var(--template-padding) * 2) [content-end] minmax(0, var(--breakout-size)) [breakout-end] minmax(var(--template-padding), 1fr) [full-width-end];
}

.content-grid > :not(.breakout, .full-width),
.full-width > :not(.breakout, .full-width) {
  grid-column: content;
}

.content-grid > .breakout {
  grid-column: breakout;
}

.content-grid > .full-width {
  grid-column: full-width;
  display: grid;
  grid-template-columns: inherit;
}

:root {
  --template-padding: 48px;
  --template-max-width: 1440px;
  --template-breakout-max-width: var(--template-max-width);
  --text-black: #1D232C;
  --text-blue: #254375;
  --text-accent: #D08935;
  --text-grey: #73777A;
  --bg-black: #1D232C;
  --bg-blue: #254375;
  --bg-light-blue: #D5DFE9;
  --border-radius: 24px 0px 0px 0px;
}

body {
  font-family: Inter;
  background-color: #fff;
}

main {
  position: relative;
  z-index: 1;
  background-color: #fff;
}

h1 {
  font-size: 5rem;
  font-weight: bold;
}

.blinking-cursor {
  display: inline-block;
  width: 2px;
  background-color: currentColor;
  margin-left: 8px;
  animation: blink 0.6s linear infinite alternate;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.link-accent {
  color: var(--text-accent);
}

.link-black {
  color: var(--text-black);
}

.link {
  text-decoration: none !important;
  line-height: 1.3;
  background-image: linear-gradient(90deg, currentColor 50%, transparent 50%);
  background-size: 200% 1px;
  background-position: right bottom 0.3rem;
  background-repeat: no-repeat;
  padding-block-end: 0.25em;
  transition: color 300ms, background-position 300ms;
}
.link:hover, .link:focus {
  background-position: left bottom 0.3rem;
}

.text-highlighted {
  display: inline-block;
  padding: 10px 16px;
  line-height: 1.3;
  background-color: var(--bg-light-blue);
  border: 1px solid var(--bg-light-blue);
  border-radius: 24px;
  margin-bottom: 12px;
  cursor: pointer;
}
.text-highlighted--white {
  background-color: #fff;
  color: var(--text-blue);
  border-color: var(--text-blue);
}

.accordion__header {
  font-size: 2rem;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.accordion__body {
  padding-top: 16px;
  display: none;
}
.accordion__body--flex {
  flex-wrap: wrap;
  gap: 12px;
}
.accordion__item {
  border-bottom: 1px solid var(--bg-light-blue);
  padding-bottom: 16px;
  padding-top: 16px;
}
.accordion__item.is-active .accordion__body {
  display: block;
}
.accordion__item.is-active .accordion__body--flex {
  display: flex;
}

.accordion--reverse .accordion__header {
  flex-direction: row-reverse;
  justify-content: flex-end;
  gap: 24px;
}
.accordion--reverse .accordion__item {
  border-top: 1px solid var(--bg-light-blue);
  border-bottom: none;
}

.form-row {
  margin-bottom: 32px;
}

.input-title {
  font-size: 1.125rem;
  line-height: 1.3;
  margin-bottom: 12px;
}
.input-title__required {
  color: var(--text-accent);
  font-size: 1rem;
  margin-left: 8px;
}

.input {
  display: block;
  width: 100%;
  border: 1px solid var(--bg-light-blue);
  padding: 20px 16px 19px;
  border-radius: 24px 0px 0px 0px;
}
.input:focus {
  box-shadow: inset 0 0 0 2px var(--bg-blue);
}
.input.is-error {
  border-color: #E81A0C;
}

.btn {
  position: relative;
  overflow: hidden;
  background: none;
  border: 1px solid var(--bg-light-blue);
  font-size: 1rem;
  line-height: 1.2;
  font-weight: bold;
  color: #fff;
  display: flex;
  flex-shrink: 0;
  gap: 24px;
  justify-content: space-between;
  padding: 0 24px 0 24px;
  height: 52px;
  align-items: center;
  border-radius: 24px;
  transition: all 0.3s;
}
.btn span {
  z-index: 1;
  white-space: nowrap;
  display: flex;
  align-items: center;
}
.btn:hover {
  color: var(--text-blue);
}
.btn:hover .btn__arrow {
  background-color: var(--bg-blue);
}
.btn:hover .btn__arrow svg {
  stroke: #fff;
}
.btn::after {
  border-radius: 0;
  background-color: #fff;
  content: "";
  position: absolute;
  z-index: 0;
  transition: all 0.3s;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
}
.btn:hover:after {
  width: 100%;
}
.btn__arrow {
  border-radius: 32px;
  width: 32px;
  height: 32px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}
.btn__arrow svg {
  stroke: var(--text-blue);
  width: 24px;
  height: 24px;
}

.btn-arrow {
  padding-right: 16px;
}

.btn-white {
  background-color: #fff;
  color: var(--black);
}
.btn-white::after {
  background-color: var(--bg-blue);
}
.btn-white:hover {
  border-color: var(--bg-blue);
  color: #fff;
}
.btn-white:hover .icon-svg,
.btn-white:hover .btn__arrow {
  stroke: #fff;
  color: #fff;
}

.btn-light-blue {
  background-color: var(--bg-light-blue);
  color: var(--black);
}
.btn-light-blue::after {
  background-color: var(--bg-blue);
}
.btn-light-blue:hover {
  border-color: var(--bg-blue);
  color: #fff;
}
.btn-light-blue:hover .icon-svg,
.btn-light-blue:hover .btn__arrow {
  stroke: #fff;
  color: #fff;
}

.btn-blue {
  background-color: var(--bg-blue);
  color: #fff;
}
.btn-blue .btn__arrow {
  stroke: var(--text-blue);
}
.btn-blue:hover {
  color: var(--text-black);
}
.btn-blue:hover .btn__arrow {
  background-color: var(--bg-blue);
  stroke: #fff;
}
.btn-blue::after {
  background-color: #fff;
}

.btn-language {
  padding-left: 24px;
  padding-right: 24px;
  font-weight: normal;
}

.btn-plus,
.btn-minus {
  position: relative;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  border: none;
}
.btn-plus .btn__line,
.btn-minus .btn__line {
  width: 22px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: inherit;
  background-color: currentColor;
  border-radius: 2px;
  transition: all 0.1s;
}
.btn-plus .btn__line:last-child,
.btn-minus .btn__line:last-child {
  transform: translate(-50%, -50%) rotate(90deg);
}

.btn-minus .btn__line:last-child {
  transform: translate(-50%, -50%) rotate(0deg);
}

.btn-header {
  width: 100px;
  height: 52px;
  background-color: #fff;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.btn-header__line {
  width: 24px;
  height: 1px;
  background-color: var(--bg-blue);
  margin: 3px 0;
  transition: 0.2s ease-in-out;
  position: relative;
}

.title {
  font-size: 3rem;
  font-weight: bold;
  padding-bottom: 32px;
  max-width: 30ch;
}
.title__border {
  width: 100%;
  border-bottom: 1px solid var(--bg, #D5DFE9);
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.subtitle {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 24px;
  display: flex;
  line-height: 1.3;
}
.subtitle__icon {
  color: var(--text-blue);
  width: 24px;
  height: 24px;
  vertical-align: sub;
  margin-right: 12px;
  flex-shrink: 0;
}

.subtitle--white {
  color: #fff;
  border-color: rgba(255, 255, 255, 0.25);
}
.subtitle--white .subtitle__icon {
  color: inherit;
}

.border-radius {
  border-radius: 24px 0 0 0;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  color: #fff;
  padding-top: 40px;
  transition: background 0.1s;
}
.header__main {
  display: flex;
  justify-content: space-between;
}
.header__main-right, .header__main-left {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  gap: 16px;
  width: 400px;
}
.header__main-right {
  justify-content: flex-end;
}
.header__contacts {
  border-left: 1px solid rgba(255, 255, 255, 0.25);
  padding: 5px 16px;
  display: flex;
  flex-direction: column;
}
.header__contacts-link {
  line-height: 1.2;
}
.header__logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 128px;
  padding-right: 16px;
}
.header__logo-picture {
  background: url("./../images/logo-white.svg") no-repeat center center/contain;
  width: 100%;
  height: 48px;
}
.header__expanded {
  display: none;
  padding-top: 100px;
}
.header__expanded-nav {
  display: flex;
  justify-content: space-between;
  gap: 140px;
}
.header__nav-address {
  background-color: var(--bg-light-blue);
  padding: 24px;
}
.header__nav-address-title {
  font-size: 0.875rem;
  color: var(--text-grey);
  margin-bottom: 12px;
  line-height: 1.3;
}
.header__nav-address p {
  font-size: 1.125rem;
  margin-bottom: 48px;
  line-height: 1.3;
}
.header__nav-address p.is-last, .header__nav-address p:last-child {
  margin-bottom: 0;
}
.header__nav-address .btn {
  display: none;
}
.header__nav-column {
  display: flex;
  flex-direction: column;
  gap: 12px;
  min-width: 318px;
}
.header__nav-column:last-child {
  grid-column: 1/3;
}
.header__nav-title {
  font-size: 0.875rem;
  color: var(--text-grey);
  padding-bottom: 16px;
  margin-bottom: 4px;
  border-bottom: 1px solid var(--bg-light-blue);
}
.header__nav-link {
  font-size: 2rem;
  font-weight: bold;
  line-height: 1.3;
  align-self: flex-start;
  background-size: 200% 2px;
}
.header__copyright-wrapper {
  font-size: 0.875rem;
  line-height: 1.3;
  color: var(--text-grey);
  padding-top: 24px;
  margin-top: 80px;
  border-top: 1px solid var(--bg-light-blue);
}
.header.is-fixed {
  position: fixed;
}
.header.is-fixed .header__expanded {
  display: block;
}
.header.is-fixed .btn-header {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn-header__line {
  background-color: #fff;
}
.header.is-fixed .btn-header__line:first-child {
  transform: rotate(45deg);
  margin-top: 3px;
}
.header.is-fixed .btn-header__line:last-child {
  transform: rotate(-45deg);
  margin-top: -3px;
}
.header.is-fixed, .header.header--inner {
  background-color: #fff;
  color: var(--text-black);
  z-index: 3;
  padding-bottom: 24px;
}
.header.is-fixed .header__logo-picture, .header.header--inner .header__logo-picture {
  background-image: url("./../images/logo-blue.svg");
}
.header.is-fixed .btn-header, .header.header--inner .btn-header {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn-header__line, .header.header--inner .btn-header__line {
  background-color: #fff;
}
.header.is-fixed .btn, .header.header--inner .btn {
  background: #fff;
  border: 1px solid var(--bg-light-blue);
  color: var(--text-black);
}
.header.is-fixed .btn__arrow, .header.header--inner .btn__arrow {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn__arrow svg, .header.header--inner .btn__arrow svg {
  stroke: #fff;
}
.header.is-fixed .btn:hover:after, .header.header--inner .btn:hover:after {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn:hover, .header.header--inner .btn:hover {
  color: #fff;
  border-color: var(--bg-light-blue);
}
.header.is-fixed .btn:hover .btn__arrow, .header.header--inner .btn:hover .btn__arrow {
  background-color: #fff;
}
.header.is-fixed .btn:hover .btn__arrow svg, .header.header--inner .btn:hover .btn__arrow svg {
  stroke: var(--text-blue);
}

.header--inner {
  position: relative;
}

.orbit {
  position: absolute;
  z-index: 1;
  width: 1000px;
  height: 1000px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.planet {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  top: calc(50% - 4px);
  left: calc(50% - 4px);
  animation: planet 10s linear infinite;
}

@keyframes planet {
  0% {
    transform: rotate(0deg) translateX(-500px) rotate(0deg);
  }
  100% {
    transform: rotate(360deg) translateX(-500px) rotate(-360deg);
  }
}
.section {
  padding-top: 140px;
  padding-bottom: 140px;
}
.section__wrapper {
  display: flex;
  flex-direction: column;
  align-items: start;
}
.section--blue {
  background-color: var(--bg-blue);
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}
.section-main {
  height: 100vh;
  height: 100svh;
  color: #fff;
  overflow: hidden;
  position: relative;
  padding-top: 92px;
  padding-bottom: var(--template-padding);
}
.section-main__annoying-btn {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 72px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.25);
  animation-name: bounce;
  animation-fill-mode: both;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
.section-main__annoying-btn img {
  width: auto;
}
.section-main__bg-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.section-main__bg {
  grid-column: full-width !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0;
  transition: 1s opacity;
  background-image: var(--bg);
}
@media (max-width: 1279px) {
  .section-main__bg {
    background-image: var(--bg-tablet, var(--bg));
  }
}
@media (max-width: 767px) {
  .section-main__bg {
    background-image: var(--bg-phone, var(--bg));
  }
}
.section-main__bg.is-active {
  opacity: 1;
}
.section-main .orbit-left {
  left: -300px;
  bottom: -400px;
}
.section-main .orbit-right {
  right: -320px;
  top: -350px;
}
.section-main .section__wrapper {
  z-index: 1;
  position: relative;
}
.section-main::before {
  display: block;
  content: "";
  background: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
}
.section-main::after {
  display: block;
  content: "";
  background: linear-gradient(0deg, rgba(29, 35, 44, 0) 0%, #1D232C 100%);
  opacity: 0.5;
  width: 100%;
  height: 200px;
  left: 0;
  right: 0;
  position: absolute;
}
.section-main__title-container {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 24px;
  justify-content: center;
}
.section-main__list {
  font-size: 1.3125rem;
}
.section-main__list-item {
  line-height: 1.3;
  margin-bottom: 12px;
}
.section-main__list-item-arrow {
  width: 24px;
  height: 24px;
  stroke: #fff;
  vertical-align: sub;
  margin-right: 12px;
  display: inline-block;
}

.section-main--inner {
  height: 500px;
}
.section-main--inner .orbit-left {
  left: -360px;
  bottom: -650px;
}
.section-main--inner .orbit-right {
  right: -500px;
  top: -440px;
}
.section-main--inner .section-main__title-container {
  justify-content: flex-end;
}
.section-main--inner h1 {
  font-size: 3.75em;
}

.stats-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1px;
}
.stats-cards__card {
  max-width: 500px;
  background-color: var(--bg-blue);
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.stats-cards__card:first-child {
  border-top-left-radius: 24px;
}
.stats-cards__card:last-child {
  border-top-right-radius: 24px;
}
.stats-cards__header {
  font-size: 4.25rem;
  font-weight: bold;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  padding-bottom: 16px;
  margin-bottom: 16px;
}
.stats-cards__body {
  line-height: 1.3;
}

.section-projects .project {
  flex: 0 0 318px;
}

.projects-slider {
  width: 100%;
}
.projects-slider .projects {
  display: flex;
  overflow: hidden;
  gap: 24px;
}

.projects {
  display: grid;
  grid-template-columns: [line-start] 1fr 1fr 1fr 1fr [line-end];
  column-gap: 24px;
  row-gap: 48px;
}
.projects .project__line {
  border-bottom: 1px solid var(--bg-light-blue);
  grid-column: line;
}

.slider-buttons {
  display: flex;
  gap: 12px;
}
.slider-buttons__prev::after {
  left: unset;
  right: 0;
}
.slider-buttons__prev svg {
  transform: rotate(180deg);
}
.slider-buttons > * {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: none;
}
.slider-buttons > * svg {
  width: 24px;
  height: 24px;
  stroke: var(--text-blue);
}

.project {
  position: relative;
}
.project__image-wrapper {
  margin-bottom: 24px;
  position: relative;
}
.project__image {
  width: 100%;
}
.project__goto-btn {
  position: absolute;
  right: 24px;
  bottom: 24px;
  border-radius: 24px;
  display: flex;
  align-items: center;
  height: 52px;
  padding: 0px 24px;
  background: #fff;
  border: none;
}
.project__goto-btn .icon-svg {
  width: 24px;
  height: 24px;
  stroke: var(--text-blue);
}
.project__title {
  font-size: 1.3125rem;
  font-weight: bold;
  line-height: 1.3;
  margin-bottom: 18px;
}
.project__title .link {
  background-size: 200% 2px;
}
.project__link {
  display: inline-block;
}
.project__link:hover .btn {
  border-color: var(--bg-blue);
}
.project__link:hover .btn::after {
  width: 100%;
}
.project__link:hover .icon-svg {
  stroke: #fff;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  border-radius: 24px;
  border: 1px solid var(--bg-light-blue);
  display: flex;
  padding: 4px 12px;
  align-items: center;
  gap: 4px;
  font-size: 0.875rem;
  line-height: 1.3;
}
.tag__icon {
  flex: 0 0 24px;
  width: 24px;
  height: 24px;
  stroke: var(--text-blue);
}

.section-map {
  background-color: var(--bg-light-blue);
}
.section-map__title-link {
  color: #36568D;
  border-bottom: 1px solid rgba(37, 67, 117, 0.25);
}
.section-map .title__border {
  border-color: #BFC9D2;
}

.map {
  max-height: 779px;
  position: relative;
}
.map__image {
